
@import "../scss/variables";

.main-content.side-content {
  transition: margin-right 0.3s ease;
}

.sidemenu-label {
  transition: margin-right 0.45s ease;
}

.side-menu {
  .nav-link:hover {
    .sidemenu-label {
      margin-left: 5px;
      transition: margin-right 0.45s ease;
    }

    color: #8760fb;
  }

  .nav-item {
    transition-duration: 0.25s;
  }

  .nav-link {
    &.with-sub {
      transition: all .25s;
    }

    transition: all .25s;
  }

  .nav-sub {
    max-height: 0;
    overflow: hidden;
    transition: all 0.9s;
  }

  .nav-item.show > .nav-sub {
    max-height: 100vh;
    transition: all 2s;
  }

  .nav-sub-link {
    transition: all .25s;
  }
}

.iconbtn {
  display: none;
}

.side-menu {
  i.angle {
    -webkit-transform-origin: center;
    -ms-transform-origin: center;
    transform-origin: center;
    position: absolute;
    top: 20px;
    left: -15px;
    font-size: 12px;
  }

  .nav-item.show i.angle {
    -webkit-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    transform: rotate(-90deg);
    position: absolute;
    top: 25px;
    left: -9px;
  }
}

.ps--active-x>.ps__rail-x, .ps--active-y>.ps__rail-y {
  display: none !important;
}

.side-badge, .main-sidebar-open.main-sidebar-hide .side-badge {
  position: absolute;
  top: 12px;
  left: 0;
  border-radius: 50px;
}

.main-sidebar-hide .side-badge {
  position: absolute;
  top: 0px;
  left: 20px;
  border-radius: 50px;
}

.sidemenu-logo {
  padding: 8px;
  margin: 0 auto;
}

.side-menu .header-brand-img {
  margin-top: 0;
}

.main-sidebar-body {
  border-top: 1px solid  #e1e6f1;
}

.side-header {
  position: sticky;
  width: 100%;
  z-index: 10;
  border-bottom: 1px solid #e1e6f1;
  box-shadow: none;
}

.sidemenu-height {
  min-height: 70vh;
}

.main-footer {
  transition: margin-right 0.3s ease;
}

