@media (min-width: 1200px) {
  .side-content {
    margin-right: 240px;
  }
}

@media (min-width: 992px) {
  .side-header .main-logo {
    color: inherit;
    margin-right: 0;
    font-size: 1.25rem;
    white-space: nowrap;
    font-weight: 600;
    padding: 0;
    transition: .3s opacity;
    line-height: 2.9rem;
    min-width: 200px;
    text-align: center;
    text-align: center;
  }

  .main-content.side-content {
    margin-right: 240px;
  }

  .main-sidebar.side-menu {
    width: 240px;
  }

  .main-footer {
    margin-right: 240px;
  }

  .main-sidebar-hide .main-footer {
    margin-right: 0px;
  }
}

@media (max-width: 992px) and (min-width: 767px) {
  .main-header-menu-icon {
    margin-left: 15px;
  }
}

@media (max-width: 767px) {
  .main-header-menu-icon {
    margin-right: 15px;
  }

  .side-header .icon-logo {
    margin-right: 0;
  }

  .main-sidebar-show .side-menu {
    top: 0px;
  }
}

@media (max-width: 992px) {
  .main-sidebar-show {
    .sidemenu-logo {
      padding: 5px;
    }

    .main-logo .header-brand-img {
      margin-right: 0px;
    }
  }
}

*::-ms-backdrop, .side-header {
  position: relative !important;
}

@media (max-width: 991.98px) {
	.main-sidebar-show {
		overflow: inherit;
	}
	.main-sidebar-show .main-sidebar {
		display: flex;
		transform: translateX(0);
		visibility: visible;
		transition: right 0.3s ease, width 0.3s ease;
		z-index: 1000;
	}
	.main-sidebar-show .main-navbar-backdrop {
		visibility: visible;
		opacity: 1;
	}
}


